export const validators = {

  /**
   * 
   * ruleformfiledrule
   * value
   * callback,Error
   * */
  'required': function(rule, value, callback) {
    if (value === null || value === undefined || value === '' || value.length < 1) {
      callback(new Error(''));
    } else {
      callback();
    }
  },

  /**
   * 
   * ruleformfiledrule
   * value
   * callback,Error
   * */
  'number': function(rule, value, callback) {
    var reg = /^[\d\.\,-]*$/;
    if (value && reg.test(value)) {
      callback();
    } else if (value && !reg.test(value)) {
      callback(new Error(''));
    } else {
      callback();
    }
  },
  /**
   * 
   * ruleformfiledrule
   * value
   * callback,Error
   * */
  'age': function(rule, value, callback) {
    var reg = /^\d+$/;
    if (value && reg.test(value)) {
      var _age = parseInt(value, 10);
      if (_age < 200) {
        callback();
      } else {
        callback(new Error(''));
      }
    } else if (value && !reg.test(value)) {
      callback(new Error(''));
    } else {
      callback();
    }
  },
  /**
   * 
   * ruleformfiledrule
   * value
   * callback,Error
   * */
  'postcode': function(rule, value, callback) {
    // var reg = /^[1-9]\d{5}$/;
    var reg = /^[0-9]{6}$/; // 0
    if (value && reg.test(value)) {
      callback();
    } else if (value && !reg.test(value)) {
      callback(new Error(''));
    } else {
      callback();
    }
  },
  /**
   * ip
   * ruleformfiledrule
   * value
   * callback,Error
   * */
  'ip': function(rule, value, callback) {
    var reg = /^(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])$/;
    if (value && reg.test(value)) {
      callback();
    } else if (value && !reg.test(value)) {
      callback(new Error('ip'));
    } else {
      callback();
    }
  },
  /**
   * 
   * ruleformfiledrule
   * value
   * callback,Error
   * */
  'telephone': function(rule, value, callback) {
    var reg = /(^\d{3}\-\d{7,8}$)|(^\d{4}\-\d{7,8}$)|(^\d{3}\d{7,8}$)|(^\d{4}\d{7,8}$)|(^\d{7,8}$)/;
    if (value && reg.test(value)) {
      callback();
    } else if (value && !reg.test(value)) {
      callback(new Error(''));
    } else {
      callback();
    }
  },
  /**
   * 
   * ruleformfiledrule
   * value
   * callback,Error
   * */
  'phone': function(rule, value, callback) {
    var reg = /(^\d{3}\-1[3458][0-9]\d{8}$)|(^\d{2}\-1[3458][0-9]\d{8}$)/;
    if (value && reg.test(value)) {
      callback();
    } else if (value && !reg.test(value)) {
      callback(new Error(''));
    } else {
      callback();
    }
  },
  /**
   * 
   * ruleformfiledrule
   * value
   * callback,Error
   * */
  'numberAndLetter': function(rule, value, callback) {
    var reg = /(^[A-Za-z0-9]+$)|([A-Za-z]+$)|([0-9]+$)/;
    if (value && reg.test(value)) {
      callback();
    } else if (value && !reg.test(value)) {
      callback(new Error(''));
    } else {
      callback();
    }
  },
  /**
   * 
   * ruleformfiledrule
   * value
   * callback,Error
   * */
  'mobile': function(rule, value, callback) {
    var reg = /^1[3-9][0-9]\d{8}$/;
    if (value && reg.test(value)) {
      callback();
    } else if (value && !reg.test(value)) {
      callback(new Error(''));
    } else {
      callback();
    }
  },
  /**
   * 
   * ruleformfiledrule
   * value
   * callback,Error
   * */
  'IDCard': function(rule, value, callback) {
    var ereg = new RegExp();
    if (!value) {
      callback();
    } else {
      var area = {
        11: '',
        12: '',
        13: '',
        14: '',
        15: '',
        21: '',
        22: '',
        23: '',
        31: '',
        32: '',
        33: '',
        34: '',
        35: '',
        36: '',
        37: '',
        41: '',
        42: '',
        43: '',
        44: '',
        45: '',
        46: '',
        50: '',
        51: '',
        52: '',
        53: '',
        54: '',
        61: '',
        62: '',
        63: '',
        64: '',
        65: '',
        71: '',
        81: '',
        82: '',
        91: ''
      };
      var Y, JYM;
      var S, M;
      var idcard_array = [];
      idcard_array = value.split('');
      if (area[parseInt(value.substr(0, 2), 10)] == null) {
        callback(new Error(''));
      }
      // 
      switch (value.length) {
        case 15:
          if ((parseInt(value.substr(6, 2), 10) + 1900) % 4 === 0 || ((parseInt(value.substr(6, 2), 10) + 1900) % 100 === 0 && (parseInt(value.substr(6, 2), 10) + 1900) % 4 === 0)) {
            ereg = /^[1-9][0-9]{5}[0-9]{2}((01|03|05|07|08|10|12)(0[1-9]|[1-2][0-9]|3[0-1])|(04|06|09|11)(0[1-9]|[1-2][0-9]|30)|02(0[1-9]|[1-2][0-9]))[0-9]{3}$/;// 
          } else {
            ereg = /^[1-9]\d{5}(18|19|20)\d{2}((0[1-9])|(1[0-2]))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/;
          }
          if (ereg.test(value)) {
            callback();
          } else {
            callback(new Error(''));
          }
          break;
        case 18:
          // 18
          // 
          // :((01|03|05|07|08|10|12)(0[1-9]|[1-2][0-9]|3[0-1])|(04|06|09|11)(0[1-9]|[1-2][0-9]|30)|02(0[1-9]|[1-2][0-9]))
          // :((01|03|05|07|08|10|12)(0[1-9]|[1-2][0-9]|3[0-1])|(04|06|09|11)(0[1-9]|[1-2][0-9]|30)|02(0[1-9]|1[0-9]|2[0-8]))
          if (parseInt(value.substr(6, 4), 10) % 4 === 0 || (parseInt(value.substr(6, 4), 10) % 100 === 0 && parseInt(value.substr(6, 4), 10) % 4 === 0)) {
            ereg = /^[1-9][0-9]{5}19[0-9]{2}((01|03|05|07|08|10|12)(0[1-9]|[1-2][0-9]|3[0-1])|(04|06|09|11)(0[1-9]|[1-2][0-9]|30)|02(0[1-9]|[1-2][0-9]))[0-9]{3}[0-9Xx]$/;// 
          } else {
            ereg = /^[1-9][0-9]{5}19[0-9]{2}((01|03|05|07|08|10|12)(0[1-9]|[1-2][0-9]|3[0-1])|(04|06|09|11)(0[1-9]|[1-2][0-9]|30)|02(0[1-9]|1[0-9]|2[0-8]))[0-9]{3}[0-9Xx]$/;// 
          }
          if (ereg.test(value)) {// 
            // 
            S = (parseInt(idcard_array[0], 10) + parseInt(idcard_array[10], 10)) * 7 + (parseInt(idcard_array[1], 10) + parseInt(idcard_array[11], 10)) * 9 + (parseInt(idcard_array[2], 10) + parseInt(idcard_array[12], 10)) * 10 + (parseInt(idcard_array[3], 10) + parseInt(idcard_array[13], 10)) * 5 + (parseInt(idcard_array[4], 10) + parseInt(idcard_array[14], 10)) * 8 + (parseInt(idcard_array[5], 10) + parseInt(idcard_array[15], 10)) * 4 + (parseInt(idcard_array[6], 10) + parseInt(idcard_array[16], 10)) * 2 + parseInt(idcard_array[7], 10) * 1 + parseInt(idcard_array[8], 10) * 6 + parseInt(idcard_array[9], 10) * 3;
            Y = S % 11;
            M = 'F';
            JYM = '10X98765432';
            M = JYM.substr(Y, 1);// 
            if (M === idcard_array[17]) {
              callback();
            } else {
              callback(new Error(',x'));
            }
          } else {
            callback(new Error(''));
          }
          break;
        default:
          callback(new Error(',1518'));
          break;
      }
    }
  },
  /**
   * 
   * ruleformfiledrule
   * value
   * callback,Error
   * */
  'isChnChar': function(rule, value, callback) {
    var reg = /[\u4E00-\u9FA5]/;
    if (value && reg.test(value)) {
      callback();
    } else if (value && !reg.test(value)) {
      callback(new Error(''));
    } else {
      callback();
    }
  },
  /**
   * 
   * ruleformfiledrule
   * value
   * callback,Error
   * */
  'trim': function(rule, value, callback) {
    if (value !== value.trim()) {
      callback(new Error(''));
    } else {
      callback();
    }
  },
  /**
   * 
   * ruleformfiledrule
   * value
   * callback,Error
   * */
  'email': function(rule, value, callback) {
    var reg = /[A-Za-z0-9_-]+[@](\S*)(net|com|cn|org|cc|tv|[0-9]{1,3})(\S*)/g;
    if (value && reg.test(value)) {
      callback();
    } else if (value && !reg.test(value)) {
      callback(new Error(''));
    } else {
      callback();
    }
  },
  /**
   * 
   * ruleformfiledrule
   * value
   * callback,Error
   * */
  'digit': function(rule, value, callback) {
    var reg = /^-?\d+(\.\d+)?$/g;
    if (value && reg.test(value)) {
      callback();
    } else if (value && !reg.test(value)) {
      callback(new Error(''));
    } else {
      callback();
    }
  },
  /**
   * 
   * ruleformfiledrule
   * value
   * callback,Error
   * */
  'pInt': function(rule, value, callback) {
    var reg = /^\+?[1-9][0-9]*$/;
    if (value && reg.test(value)) {
      callback();
    } else if (value && !reg.test(value)) {
      callback(new Error(''));
    } else {
      callback();
    }
  },
  /**
   * 0 
   * ruleformfiledrule
   * value
   * callback,Error
   * */
  'gZero': function(rule, value, callback) {
    var reg = /^[\+]?[0-9]*\d(\.\d+)?$/;
    if (value && reg.test(value)) {
      callback();
    } else if (value && !reg.test(value)) {
      callback(new Error(''));
    } else {
      callback();
    }
  },
  /**
   * 
   * ruleformfiledrule
   * value
   * callback,Error
   * */
  'speChar': function(rule, value, callback) {
    var reg = new RegExp("[`~!@#$^&*()=|{}':; ',\\[\\].<>/?~@#&*|{}']");
    if (value && reg.test(value)) {
      callback(new Error(''));
    } else {
      callback();
    }
  }
};
